@font-face {
  font-family: 'Schadow';
  font-stretch: condensed;
  src: url(../src/Schadow/SchadowBlackCondensed.ttf);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p, .nav-link, table {
  font-family: 'Spectral', serif;
}

.cover {
  height: 420px;
  box-shadow: 0 19px 38px rgb(232, 232, 201), 0 15px 12px rgb(232, 232, 201);
  text-align: left;
  margin-bottom: 10px;
}

.back {
  height: 550px;
  -moz-box-shadow: 14px 14px 12px #000000;
  -webkit-box-shadow: 14px 14px 12px #000000;
  box-shadow: 14px 14px 12px #000000;
}

.jumbotron {
  background-color: #173041 !important;
  color: #e8e8c9;
  margin-bottom: 3rem !important;
  font-family: "Schadow";
}

.jumbotron h1 {
  font-size: 90px;
}

.jumbotron h2 {
  font-size: 70px;
}

.jumbotron_secondary {
  padding: 3rem 2rem !important;
}

.nav {
  font-size: 18px;
  font-weight: 700;
  margin-right: 5px;
}

.nav-link {
  color: #173040c2;
}

.nav-link:hover, .footer, .footer a {
  color: #e8e8c9 !important;
}

.table {
  font-size: 1.2rem;
}

.footer a {
  text-decoration: underline;
}

.nav-item a {
  line-height: 34px;
}

.big-W {
  font-size: 50px;
  margin-right: -5px;
  line-height: 28px;
}

p {
  line-height: 30px;
  text-align: left;
}

.img0 {
  position: absolute;
  width: 400px;
  height: auto;
  left: 0;
  -moz-box-shadow: 14px 14px 12px #000000;
  -webkit-box-shadow: 14px 14px 12px #000000;
  box-shadow: 14px 14px 12px #000000;
  border-radius: 5px;
}

.img1 {
  position: absolute;
  width: 400px;
  height: auto;
  right: 40px;
  bottom: 0;
  -moz-box-shadow: 14px 14px 12px #000000;
  -webkit-box-shadow: 14px 14px 12px #000000;
  box-shadow: 14px 14px 12px #000000;
  border-radius: 5px;
}

.footer {
  height: 225px;
  margin: 55px 0 0 0 !important;
  font-family: 'Spectral', serif;
  text-align: left;
  padding: 0 10% !important;
  position: fixed;
  width: 100%;
  bottom: 0;
}

.footer h4 {
  margin-bottom: 20px;
}

.footer ul {
  padding: 0;
}

.footer li {
  list-style: none;
  margin-bottom: 10px;
}

input {
  width: 80%;
  border-radius: 5px;
  height: 40px;
  padding: 5px 15px;
  margin-right: 10px !important;
}

.fa-check-square {
  position: absolute;
  font-size: 40px !important;
  cursor: pointer;
}

hr {
  border: 0;
  border-top: 5px solid !important;
  color: #173040c2;
  margin-top: 0.3rem !important;
  margin-bottom: 0.3rem !important;
}

.hr1 {
  margin-bottom: 50px !important;
}

.author {
  color: #173041 !important;
  font-weight: bold;
  font-weight: 600 !important;
  margin-top: 15px;
  margin-bottom: 30px;
}

.profile {
  height: 400px;
  width: auto;
  border-radius: 5px;
  ;
}

img {
  border-radius: 3px;
}

/* Utility Classes */

.textCenter {
  text-align: center;
}

.textLeft {
  text-align: left;
}

.textRight {
  text-align: right;
}

.justifyCenter {
  justify-content: center;
}

.shortDownSpacer {
  margin-bottom: 50px;
}

.bloomsWheel {
  width: 650px;
  height: 650px;
  -webkit-animation: spin 2s linear infinite;
  -moz-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
}

.bloomsWheelStopped {
  height: auto;
  max-width: 650px;
}

/* animations */

@-moz-keyframes spin {
  100% {
    -moz-transform: rotate(360deg);
  }
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}